<template>
  <div class="home">
    <Header></Header>
    <CovidInfo></CovidInfo>
    <CaseNum :caseNumDate="caseNumDate"></CaseNum>
    <OutbreakMap></OutbreakMap>
    <YiqingSwiper :news="news"></YiqingSwiper>
  </div>
</template>

<script>
// @ is an alias to /src
import Header from "../components/Header";
import CovidInfo from "../components/CovidInfo";
import CaseNum from "../components/CaseNum";
import OutbreakMap from "@/components/OutbreakMap";
import YiqingSwiper from "../components/YiqingSwiper";
export default {
  name: "HomeView",
  components: { Header, CovidInfo, CaseNum, OutbreakMap, YiqingSwiper },
  data() {
    return {
      caseNumDate: {
        modifyTime: "", //信息更新时间
        currentConfirmedCount: "", //现存确诊人数
        confirmedCount: "", //累计确诊人数
        suspectedCount: "", //累计境外输入人数
        curedCount: "", //累计治愈人数
        deadCount: "", //累计死亡人数
        seriousCount: "", //现存无症状人数
        /** */
        currentConfirmedIncr: "", //相比昨天现存确诊人数
        confirmedIncr: "", //相比昨天累计确诊人数
        yesterdaySuspectedCountIncr: "", //相比昨天境外输入确诊人数
        curedIncr: "", //相比昨天新增治愈人数
        deadIncr: "", //相比昨天新增死亡人数
        seriousIncr: "", //相比昨天现存无症状人数
      },
      news: "",
    };
  },
  mounted() {
    /**
     * 获取疫情接口的全部数据
     */
    this.axios
      .get("http://api.tianapi.com/ncov/index", {
        params: {
          key: "ccddeef738e38af6afc11069aee1eab9",
        },
      })
      .then(
        (response) => {
          this.news = response.data.newslist[0].news;
          let desc = response.data.newslist[0].desc;
          this.caseNumDate.modifyTime = desc.modifyTime;
          this.caseNumDate.currentConfirmedCount = desc.currentConfirmedCount; //现存确诊人数
          this.caseNumDate.confirmedCount = desc.confirmedCount; //累计确诊人数
          this.caseNumDate.suspectedCount = desc.suspectedCount; //累计境外输入人数
          this.caseNumDate.curedCount = desc.curedCount; //累计治愈人数
          this.caseNumDate.deadCount = desc.deadCount; //累计死亡人数
          this.caseNumDate.seriousCount = desc.seriousCount; //现存无症状人数
          this.caseNumDate.currentConfirmedIncr = desc.currentConfirmedIncr; //相比昨天现存确诊人数
          this.caseNumDate.confirmedIncr = desc.confirmedIncr; //相比昨天累计确诊人数
          this.caseNumDate.yesterdaySuspectedCountIncr =
            desc.yesterdaySuspectedCountIncr; //相比昨天境外输入确诊人数
          this.caseNumDate.curedIncr = desc.curedIncr; //相比昨天新增治愈人数
          this.caseNumDate.deadIncr = desc.deadIncr; //相比昨天新增死亡人数
          this.caseNumDate.seriousIncr = desc.seriousIncr; //相比昨天现存无症状人数
        },
        (error) => {
          console.log("失败了", error.message);
        }
      );
  },
};
</script>
