<template>
  <div>
    <p class="title"><i></i>疫情地图</p>
    <ul class="Tabs">
      <li :class="showMap == 0 ? 'active' : ''" @click="showMap = !showMap">
        国内疫情
      </li>
      <li :class="showMap == 1 ? 'active' : ''" @click="showMap = !showMap">
        世界疫情
      </li>
    </ul>
    <div id="chinaMap" v-show="!showMap"></div>
    <div id="worldMap" v-show="showMap"></div>
  </div>
</template>

<script>
import echarts from "echarts";
import "../../node_modules/echarts/map/js/china";
import "../../node_modules/echarts/map/js/world";
import "../assets/js/nameMap";
import nameMap from "../assets/js/nameMap";
export default {
  name: "OutbreakMap",
  data() {
    return {
      showMap: 0,
    };
  },
  mounted() {
    this.axios.get("http://iwenwiki.com/wapicovid19/all.php").then(
      (response) => {
        var allCitys = [];
        response.data.retdata.forEach((element) => {
          let temp = {
            name: element.xArea,
            value: element.confirm,
          };
          allCitys.push(temp);
        });
        this.echartsInfo(allCitys);
      },
      (error) => {
        console.log("失败了", error.message);
      }
    );
    this.axios.get("http://iwenwiki.com/wapicovid19/foreign.php").then(
      (response) => {
        console.log(response);
        var worldCitys = [];
        response.data.retdata.forEach((element) => {
          let temp = {
            name: element.xArea,
            value: element.confirm,
          };
          worldCitys.push(temp);
        });
        this.worldInfo(worldCitys);
      },
      (error) => {
        console.log("失败了", error.message);
      }
    );
  },
  methods: {
    echartsInfo(data) {
      echarts.init(document.getElementById("chinaMap")).setOption({
        // 提示信息
        tooltip: {
          triggerOn: "click", // 事件类型
          enterable: true, // 鼠标是否可进入提示框浮层中，默认为false，如需详情内交互，如添加链接，按钮，可设置为 true。
          formatter(data) {
            return (
              "<div><p>" +
              data.name +
              "</p><p>现存确诊:" +
              data.value +
              "</p></div></a>"
            );
          },
        },
        visualMap: [
          {
            // 映射-颜色值
            orient: "vertical", // 分段方向:horizontal水平
            type: "piecewise", // 分段
            pieces: [
              // 配置颜色区间
              { min: 0, max: 0, color: "#FFFFFF" },
              { min: 1, max: 100, color: "#FDECE9" },
              { min: 100, max: 500, color: "#FEB1B1 " },
              { min: 500, max: 1000, color: "#FF7D7D " },
              { min: 1000, max: 10000, color: "#FF5656 " },
              { min: 10000, color: "#FF0000" },
            ],
          },
        ],
        series: [
          {
            name: "省",
            type: "map", // 配置图表类型
            map: "china", // 中国地图
            roam: false, // 是否允许自动缩放
            zoom: 1.2, // 地图缩放比例
            label: {
              // 配置字体
              normal: {
                show: true,
                textStyle: {
                  fontSize: 8,
                },
              },
            },
            itemStyle: {
              // 配置地图样式
              normal: {
                areaColor: "rgba(0,255,236,0)",
                borderColor: "rgba(0,0,0,0.2)",
              },
              emphasis: {
                // 选中的区域颜色及阴影效果等
                areaColor: "rgba(255,180,0,0.8)",
                shadowOffsetX: 0,
                shadowOffsetY: 0,
                shadowBlur: 20,
                borderWidth: 0,
              },
            },
            data: data,
          },
        ],
      });
    },
    worldInfo(data) {
      echarts.init(document.getElementById("worldMap")).setOption({
        // 提示信息
        tooltip: {
          triggerOn: "click", // 事件类型
          enterable: true, // 鼠标是否可进入提示框浮层中，默认为false，如需详情内交互，如添加链接，按钮，可设置为 true。
          formatter(data) {
            let dValue = data.value ? data.value : "待发布";
            return (
              "<div><p>" +
              data.name +
              "</p><p>现存确诊:" +
              dValue +
              "</p></div></a>"
            );
          },
        },
        visualMap: [
          {
            // 映射-颜色值
            orient: "vertical", // 分段方向:horizontal水平
            type: "piecewise", // 分段
            pieces: [
              // 配置颜色区间
              { min: 0, max: 0, color: "#FFFFFF" },
              { min: 1, max: 10000, color: "#FDECE9" },
              { min: 10000, max: 500000, color: "#FEB1B1 " },
              { min: 50000, max: 1000000, color: "#FF7D7D " },
              { min: 1000000, max: 10000000, color: "#FF5656 " },
              { min: 10000000, color: "#FF0000" },
            ],
          },
        ],
        series: [
          {
            nameMap: nameMap,
            type: "map", // 配置图表类型
            map: "world", // 中国地图
            roam: false, // 是否允许自动缩放
            zoom: 1.2, // 地图缩放比例
            label: {
              // 配置字体
              normal: {
                show: false,
                textStyle: {
                  fontSize: 8,
                },
              },
            },
            itemStyle: {
              // 配置地图样式
              normal: {
                areaColor: "rgba(0,255,236,0)",
                borderColor: "rgba(0,0,0,0.2)",
              },
              emphasis: {
                // 选中的区域颜色及阴影效果等
                areaColor: "rgba(255,180,0,0.8)",
                shadowOffsetX: 0,
                shadowOffsetY: 0,
                shadowBlur: 20,
                borderWidth: 0,
              },
            },
            data: data,
          },
        ],
      });
    },
  },
};
</script>

<style scoped>
#chinaMap {
  width: 100vw;
  height: 400px;
}
#worldMap {
  width: 100vw;
  height: 400px;
}
.title {
  border-top: 0.005rem solid #ebebeb;
  border-bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  height: 0.44rem;
  padding: 0 0.16rem;
  font-weight: 500;
  font-size: 0.17rem;
  line-height: 0.44rem;
  background: #fff;
}
.title::before {
  content: "";
  width: 5px;
  height: 20px;
  background: #4169e2;
  margin-right: 10px;
}
.Tabs {
  height: 0.44rem;
  padding: 0 0.16rem;
  display: flex;
  align-items: center;

  line-height: 0.44rem;
  justify-content: space-around;
  background-color: #fff;
}
.Tabs li {
  font-size: 0.16rem;
  width: 50%;
  text-align: center;
}
li.active {
  border-bottom: 2px solid #4169e2;
}
</style>